<template>
    <div id="app">
        <!--        <dashboard config-url="/Config/Dashboard" info-url="/Auth/GetInfo" logout-url="/Auth/Logout"-->
        <!--                   @ready="onDashboardReady">-->
        <div class="dashboard">
            <div class="pagewrapper">
                <div class="pagecontent">
                    <div class="contentwrapper">
                        <template v-if="ready">
                            <router-view></router-view>

                        </template>
                    </div>
                </div>
            </div>
            <div class="incidentBox" v-if="displayMaintenance && maintenanceHeading">
                <span class="alert-icon incident-icon mr-2"></span>
                <div class="incident-body">
                    <div class="incidentHeading">
                        <span class="align-middle incident-text">{{ this.maintenanceHeading }}</span>
                    </div>
                    <div class="incidentTime">
                        {{ this.maintenanceTimeDate }} from {{ this.maintenanceTimeStart }} to {{ this.maintenanceTimeEnd }}
                    </div>
                    <div>
                        <a class="incidentUrl" href="https://affinityteam.statuspage.io/" target="_blank">View Latest
                            Update</a>
                    </div>
                </div>
                <span class="cross-icon incident-icon ml-2" @click="closeIncident()"></span>
            </div>
            <div class="incidentBox incidentBox-incident" v-else-if="displayIncident && incidentsHeading"
                 :class="{'incidentBox-incident': incidentInpact === 'minor', 'incidentBox-incident-major': incidentInpact !== 'minor'}">
                <span class="alert-icon incident-icon mr-2"></span>
                <div class="incident-body">
                    <div class="incidentHeading">
                        <span class="align-middle incident-text">{{ this.incidentsHeading }}</span>
                    </div>
                    <div class="incidentTime">
                        Created at {{ this.incidentsTimeStart }}
                    </div>
                    <div>
                        <a class="incidentUrl" :href="this.incidentsshortLink" target="_blank">View Latest Update</a>
                    </div>
                </div>
                <span class="cross-icon incident-icon ml-2" @click="closeIncident()"></span>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
import { statusPageAPI } from "@/remote/api";

export default {
    data() {
        return {
            ready: false,
            localConfig: false,

            displayMaintenance: true,
            maintenanceHeading: '',
            maintenanceTimeStart: '',
            maintenanceTimeEnd: '',
            maintenanceTimeDate: '',

            displayIncident: true,
            incidentsHeading: '',
            incidentsTimeStart: '',
            incidentsshortLink: '',
            incidentInpact: ''
        }
    },
    watch: {
        $config: {
            handler(val) {
                this.onDashboardReady(val)
            },
        }
    },
    mounted() {
        axios.get(statusPageAPI).then((response) => {
            if (response.data.incidents[0]) {
                this.displayIncident = true;
                this.displayMaintenance = false;
                this.incidentsHeading = response.data.incidents[0].name;
                this.incidentsTimeStart = moment(response.data.incidents[0].created_at).format('DD MMM YYYY HH:mm');
                this.incidentsshortLink = response.data.incidents[0].shortlink;
                this.incidentInpact = response.data.incidents[0].impact;
                return
            }
            if (response.data.scheduled_maintenances && response.data.scheduled_maintenances[0]) {
                this.displayMaintenance = true;
                this.displayIncident = false;
                this.maintenanceHeading = response.data.scheduled_maintenances[0].name;
                this.maintenanceTimeDate = moment(response.data.scheduled_maintenances[0].scheduled_for).format('DD MMM YYYY');
                this.maintenanceTimeStart = moment(response.data.scheduled_maintenances[0].scheduled_for).format('HH:mm');
                this.maintenanceTimeEnd = moment(response.data.scheduled_maintenances[0].scheduled_until).format('HH:mm');
                return
            }
        })
    },
    methods: {
        onDashboardReady(config) {
            this.ready = true;
            // this.$widgets = [];
            // this.$apps = {};
            config.Apps.forEach((app) => {
                if (app.Widgets) {
                    app.Widgets.forEach((w) => {
                        if (w.Enabled) {
                            if (this.$user.EmployeeNo >= 5000000 && w.Name != 'Inbox') {
                                return;
                            }
                            this.$widgets.push(w.Name);
                        }
                    });
                }
                this.$apps[app.Name] = app;
            });
        },
        closeIncident() {
            this.displayIncident = false;
        }
    }
}
</script>

<style>
#app {
    height: 100%;
}

.incident-icon {
    display: inline-block;
    width: 18px;
    height: 18px;
    margin-top: 2px;
    vertical-align: top;
}
.ml-2 {
    margin-left: 8px
}

.mr-2 {
    margin-right: 8px
}

.alert-icon {
    vertical-align: top;
    background-image: url("https://cdn.jsdelivr.net/gh/affinityteam/www@23.30.0.16455t/lg/img/exclamation-mark-white.svg");
    background-size: cover;
    background-position: center;
}

.cross-icon {
    background-image: url("https://cdn.jsdelivr.net/gh/affinityteam/www@23.30.0.16455t/lg/img/cross-svg.svg");
    background-size: cover;
    background-position: center;

    cursor: pointer;
}

.align-middle {
    vertical-align: middle;
}

.incidentBox {
    position: fixed;
    bottom: 60px;
    right: 20px;
    max-width: 320px;
    z-index: 99999;

    color: white;
    padding: 20px;
    background-color: #3498DB;

    font-size: 14px;
}

.incidentBox-incident {
    background-color: #ff6600;
}

.incidentBox-incident-major {
    background-color: #E74C3C;
}

.incidentHeading {
    font-size: 14px;
}

.incidentTime {
    margin-top: 10px;
}

.incident-text {
    display: inline-block;
    max-width: 230px;
}

.incident-body {
    display: inline-block;
    vertical-align: top;
}

.incidentUrl {
    display: block;
    margin-top: 10px;
    color: white;
    text-decoration: underline !important;

}</style>